class Note {
  constructor(bookKey, chapter, text, cfi, range, color, note) {
    this.key = new Date().getTime() + '';
    this.bookKey = bookKey;
    this.chapter = chapter;
    this.text = text;
    this.cfi = cfi;
    this.range = range; // Rangy.jsRange
    this.color = color; // 0, 1, 2, 3
    this.note = note || '';
  }
}

export default Note;
